<template>
  <div class="con">
    <publicTop></publicTop>
    <!-- 这一块暂时不要 -->
    <!-- <div class="home-top">
      <div class="home-top-l">
        <img src="@/assets/left-le.png" @click="goB" />
      </div>
      <div class="home-top-r">
        <img
          src="@/assets/icon_collect.png"
          class="home-top-r-img1"
          v-if="!contentDetail.flag"
        />
        <img src="@/assets/icon_collected.png" class="home-top-r-img1" v-else />
        <img
          src="@/assets/icon_share.png"
          class="home-top-r-img2"
          @click="goShare"
        />
      </div>
    </div> -->

    <!-- 头部 -->
    <div
      class="home-head"
      :style="`background-image: url(${url});background-size: 100% 100%;`"
    >
      <div class="home-head-head">
        <div class="home-head-head-l" @click="goB">
          <img src="@/assets/icon-back-white.png" />
        </div>
        <div class="home-head-head-r">
          {{ contentDetail.bilateralchosefair.holdStatus }}
        </div>
      </div>
      <div class="home-head-t">
        {{ contentDetail.bilateralchosefair.title }}
      </div>
      <div class="home-head-place">
        <img src="@/assets/bai.png" />
        <div class="home-head-place-r">
          {{ contentDetail.bilateralchosefair.realPlace }}
        </div>
      </div>
      <div class="home-head-time">
        <img src="@/assets/bai1.png" />
        <div class="home-head-time-r">
          {{ contentDetail.bilateralchosefair.startTime.slice(0, 10) }}
        </div>
      </div>

      <div class="home-sky">
        <div class="home-sky-line"></div>
        <div class="home-sky-line1"></div>
        <div class="home-sky-li">
          <div class="home-sky-li-t">
            {{ contentDetail.statisticsDetailMap.corporationNum }}
          </div>
          <div class="home-sky-li-end">参会单位</div>
        </div>
        <div class="home-sky-li">
          <div class="home-sky-li-t">
            {{ contentDetail.statisticsDetailMap.positionNum }}
          </div>
          <div class="home-sky-li-end">可投职位</div>
        </div>
        <div class="home-sky-li">
          <div class="home-sky-li-t">
            {{ contentDetail.statisticsDetailMap.demandNum }}
          </div>
          <div class="home-sky-li-end">招聘人数</div>
        </div>
      </div>
    </div>

    <div class="home-message-tab">
      <div
        v-for="(item, index) in tabs"
        :key="index"
        :class="ir == index ? 'home-message-left' : 'home-message-right'"
        @click="goTab(index)"
      >
        {{ item.name }}
      </div>
    </div>
    <!-- <div v-if="ir == 0" class="list-one">
      <div class="list-one-text">{{ decc }}</div>
    </div> -->

    <div class="list-one" v-if="ir == 0">
      <div
        class="list-one-dec"
        v-if="
          isOpen4 == false &&
          contentDetail.bilateralchosefair.content &&
          contentDetail.bilateralchosefair.content != ''
        "
      >
        <div
          class="list-one-dec-text"
          ref="element"
          v-html="contentDetail.bilateralchosefair.content"
        ></div>
        <div class="ab" @click="goChD" v-if="jii != '0%'">
          <div class="ab-l">{{ tex }}</div>
          <img src="@/assets/icon-down-blue.png" class="ab-r" />
        </div>
      </div>
      <div
        class="list-one-dec1"
        :style="`display:${isPlay}`"
        v-if="
          contentDetail.bilateralchosefair.content &&
          contentDetail.bilateralchosefair.content != ''
        "
      >
        <div
          class="list-one-dec-text1"
          ref="element1"
          v-html="contentDetail.bilateralchosefair.content"
        ></div>
        <div class="ab1" @click="goChD">
          <div class="ab1-l">收起</div>
        </div>
      </div>
    </div>

    <div
      v-if="ir == 1 && contentDetail.bilateralchosefair.bilateralchosefairList"
      class="list-two"
    >
      <div
        class="list-two-li"
        v-for="(item, index) in contentDetail.bilateralchosefair
          .bilateralchosefairList"
        :key="index"
        @click="toYue(item.id)"
      >
        <img
          :src="item.corporationinfo.logoUrl"
          class="list-two-li-l"
          v-if="item.corporationinfo.logoUrl"
        />
        <img src="@/assets/momo.png" class="list-two-li-l" v-else />
        <div class="list-two-li-r">
          <div class="list-two-li-r-top">{{ item.corporationinfo.name }}</div>
          <div class="list-two-li-r-bottom">
            {{ item.corporationinfo.corporationNature }} |
            {{ item.positionNum }}个职位
          </div>
        </div>
      </div>
    </div>

    <!-- 投递简历 -->
    <div
      class="fixed-foot"
      v-if="contentDetail.bilateralchosefair.fairType == 1"
    >
      <img
        src="@/assets/toplay.png"
        class="fixed-foot-r"
        v-if="contentDetail.bilateralchosefair.orderFlag == '0'"
        @click="toPlay"
      />
      <img
        src="@/assets/tocancle.png"
        class="fixed-foot-r"
        v-if="contentDetail.bilateralchosefair.orderFlag != '0'"
        @click="toCancel"
      />
    </div>

    <!-- 选择企业 -->
    <div
      class="add-jian"
      v-if="isSecond == true && jianZhi.length != 0"
      @click="goClose2"
    >
      <div class="add-jian-div" @click.stop="gu2">
        <div class="add-jian-div-head">
          <div class="add-jian-div-head-l">选择时间段</div>
          <img src="@/assets/guan-close.png" @click="goClose2" />
        </div>
        <div class="add-jian-div-neck">
          <div
            class="add-jian-div-neck-item"
            v-for="(item, index) in jianZhi"
            :key="index"
          >
            <img
              src="@/assets/cell-select.png"
              class="add-jian-div-neck-item-l"
              v-if="item.selected1 == false"
              @click="toSelect1(index)"
            />
            <img
              src="@/assets/cell-selected.png"
              class="add-jian-div-neck-item-l"
              v-if="item.selected1 == true"
              @click="toSelect1(index)"
            />
            <div class="add-jian-div-neck-item-r">
              <div class="add-jian-div-neck-item-r-mid">
                <div class="add-jian-div-neck-item-r-mid-t">
                  时间段: {{ item.startTime }}---{{ item.endTime }}
                  {{ item.status == 0 ? "(已约满)" : "" }}
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="add-jian-div-foot">
          <img
            src="@/assets/toplay.png"
            class="add-jian-div-foot-r"
            @click="confirmTou"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import qs from "qs";
import publicTop from "@/components/publicTop.vue";
export default {
  name: "bilateralchosefairForm",
  components: {
    publicTop,
  },
  data() {
    return {
      url: require("../assets/bgB.png"),
      decc: "左手交易手续费",
      contentDetail: {},
      ir: 0,
      tabs: [{ name: "详情介绍" }, { name: "参会企业" }],
      shuangId: "",
      isOpen4: false,
      isPlay: "block",
      tex: "",
      isSecond: false,
      jianZhi: [],
      playId: "",
      jii: 9,
    };
  },
  methods: {
    goChD() {
      if (this.isOpen4 == false) {
        this.isOpen4 = true;
      } else {
        this.isOpen4 = false;
      }
      if (this.isPlay == "block") {
        this.isPlay = "none";
      } else {
        this.isPlay = "block";
      }
      console.log(this.isOpen4, this.isPlay);
    },
    goB() {
      this.$router.go(-1);
    },
    goShare() {},
    toYue(id) {
      console.log(id);
      // this.$store.commit("updataDetailYueId", id);
      // this.$router.push({ name: "shuangDetailYue" });

      this.$router.push({
        path: "/shuangDetailYue",
        query: { id: id },
      });
    },
    goTab(index) {
      this.ir = index;
    },
    goOpen() {
      this.isOpen = !this.isOpen;
    },
    goCloseSha() {
      this.isShare = false;
    },
    gS() {},
    toPlay() {
      if (this.contentDetail.bilateralchosefair.isTimezone == 1) {
        this.isSecond = true;
      } else {
        const params = {
          bilateralchosefairId: this.shuangId,
        };
        console.log(params);
        this.$axios
          // .get(
          //   "/f/bilateralchosefair/ajax_studentOrder?bilateralchosefairId=" +
          //     this.shuangId
          // )
          .post("/f/bilateralchosefair/ajax_studentOrder", qs.stringify(params))
          .then((res) => {
            console.log(res, "预约参会");
            if (res.data.state == 1) {
              this.$toast.success(res.data.msg);
              // this.getDetailContent();
              this.getll();
            } else if (res.data.state == 2) {
              // window.open(res.data.data);
              window.location.href = res.data.data;
              this.getll();
            } else {
              this.$toast(res.data.msg);
              // this.getDetailContent();
              this.getll();
            }
          })
          .catch((err) => {
            console.log(err, "获取失败");
          });
      }
    },
    toCancel() {
      const params1 = {
        bilateralchosefairId: this.shuangId,
      };
      this.$axios
        // .get(
        //   "/f/bilateralchosefair/ajax_studentOrder_cancel?bilateralchosefairId=" +
        //     this.shuangId
        // )
        .post(
          "/f/bilateralchosefair/ajax_studentOrder_cancel",
          qs.stringify(params1)
        )
        .then((res) => {
          console.log(res, "取消预约参会");
          if (res.data.state == 1) {
            this.$toast.success(res.data.msg);
            // this.getDetailContent();
            this.getll();
          } else if (res.data.state == 2) {
            // window.open(res.data.data);
            window.location.href = res.data.data;
            this.getll();
          } else {
            this.$toast(res.data.msg);
            // this.getDetailContent();
            this.getll();
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    goClose2() {
      this.isSecond = false;
      for (var i = 0; i < this.jianZhi.length; i++) {
        this.jianZhi[i].selected1 = false;
      }
    },
    gu2() {},
    toSelect1(index) {
      for (var i = 0; i < this.jianZhi.length; i++) {
        console.log(this.jianZhi[i].status);
        if (i == index) {
          if (this.jianZhi[index].status == 0) {
            this.jianZhi[index].selected1 = false;
            this.$toast("已约满");
          } else {
            if (this.jianZhi[i].selected1 == false) {
              this.jianZhi[index].selected1 = true;
            } else {
              this.jianZhi[index].selected1 = false;
            }
          }
        } else {
          this.jianZhi[i].selected1 = false;
        }
      }
    },

    confirmTou() {
      var lo2 = this.jianZhi.every((item) => {
        return item.status == 0;
      });
      var lo3 = this.jianZhi.every((item) => {
        return item.selected1 == false;
      });
      console.log(lo2, lo3);
      if (lo2 == true) {
        this.$toast("暂无时间段可选");
      } else if (lo3 == true) {
        this.$toast("请选择时间段");
      } else {
        for (var i = 0; i < this.jianZhi.length; i++) {
          if (this.jianZhi[i].selected1) {
            this.playId = this.jianZhi[i].id;
          }
          this.jianZhi[i].selected1 = false;
        }
        console.log(this.playId, "lll");
        const params2 = {
          bilateralchosefairId: this.shuangId,
          timeZoneId: this.playId,
        };
        this.$axios
          // .get(
          //   "/f/bilateralchosefair/ajax_studentOrder?bilateralchosefairId=" +
          //     this.shuangId +
          //     "&timeZoneId=" +
          //     this.playId
          // )
          .post(
            "/f/bilateralchosefair/ajax_studentOrder",
            qs.stringify(params2)
          )
          .then((res) => {
            console.log(res, "预约参会");
            if (res.data.state == 1) {
              this.isSecond = false;
              this.$toast.success(res.data.msg);
              // this.getDetailContent();
              this.getll();
            } else if (res.data.state == 2) {
              // window.open(res.data.data);
              window.location.href = res.data.data;
              this.getll();
            } else {
              this.$toast(res.data.msg);
              // this.getDetailContent();
              this.getll();
            }
          })
          .catch((err) => {
            console.log(err, "获取失败");
          });
      }
    },
    getll() {
      const params3 = {
        bilateralchosefairId: this.shuangId,
      };
      this.$axios
        // .get(
        //   "/f/bilateralchosefair/ajax_show?bilateralchosefairId=" +
        //     this.shuangId
        // )
        .post("/f/bilateralchosefair/ajax_show", qs.stringify(params3))
        .then((res) => {
          console.log(res, "详情");
          if (res.data.state == 1) {
            this.contentDetail = res.data.object;
            if (
              this.contentDetail.bilateralchosefair
                .bilateralchosefairTimeZoneList &&
              this.contentDetail.bilateralchosefair
                .bilateralchosefairTimeZoneList.length != 0
            ) {
              var weights1 = [];
              res.data.object.bilateralchosefair.bilateralchosefairTimeZoneList.forEach(
                (item) => {
                  var obj = {
                    selected1: false,
                    id: item.id,
                    bilateralchosefairId: item.bilateralchosefairId,
                    currentNumber: item.currentNumber,
                    currentProtectNumber: item.currentProtectNumber,
                    endTime: item.endTime,
                    isProtect: item.isProtect,
                    number: item.number,
                    sort: item.sort,
                    startTime: item.startTime,
                    status: item.status,
                  };
                  weights1.push(obj);
                }
              );

              this.jianZhi = weights1;
              console.log(this.jianZhi, "this.zhaopinList");
            } else {
              this.jianZhi = [];
            }
            this.contentDetail.bilateralchosefair.content =
              this.contentDetail.bilateralchosefair.content.replace(
                /<img/g,
                "<img style='max-width:100%;height:auto;'"
              );
          } else {
            this.contentDetail = {};
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getDetailContent() {
      const params4 = {
        bilateralchosefairId: this.shuangId,
      };
      this.$axios
        // .get(
        //   "/f/bilateralchosefair/ajax_show?bilateralchosefairId=" +
        //     this.shuangId
        // )
        .post("/f/bilateralchosefair/ajax_show", qs.stringify(params4))
        .then((res) => {
          console.log(res, "详情");
          if (res.data.state == 1) {
            this.contentDetail = res.data.object;
            if (
              this.contentDetail.bilateralchosefair
                .bilateralchosefairTimeZoneList &&
              this.contentDetail.bilateralchosefair
                .bilateralchosefairTimeZoneList.length != 0
            ) {
              var weights1 = [];
              res.data.object.bilateralchosefair.bilateralchosefairTimeZoneList.forEach(
                (item) => {
                  var obj = {
                    selected1: false,
                    id: item.id,
                    bilateralchosefairId: item.bilateralchosefairId,
                    currentNumber: item.currentNumber,
                    currentProtectNumber: item.currentProtectNumber,
                    endTime: item.endTime,
                    isProtect: item.isProtect,
                    number: item.number,
                    sort: item.sort,
                    startTime: item.startTime,
                    status: item.status,
                  };
                  weights1.push(obj);
                }
              );

              this.jianZhi = weights1;
              console.log(this.jianZhi, "this.zhaopinList");
            } else {
              this.jianZhi = [];
            }
            this.contentDetail.bilateralchosefair.content =
              this.contentDetail.bilateralchosefair.content.replace(
                /<img/g,
                "<img style='max-width:100%;height:auto;'"
              );

            if (res.data.object.bilateralchosefair.content) {
              setTimeout(() => {
                let height = this.$refs.element;
                console.log(
                  this.$refs.element,
                  9,
                  height,
                  height.offsetHeight,
                  6
                );
                let height1 = this.$refs.element1;
                console.log(height1.offsetHeight, 63);
                this.isPlay = "none";
                this.Calculation(
                  "reduce",
                  height1.offsetHeight,
                  height.offsetHeight
                );
                var temp = this.Calculation(
                  "reduce",
                  height1.offsetHeight,
                  height.offsetHeight
                );
                console.log(temp, 4);
                if (temp <= 0) {
                  var jo = "0%";
                } else {
                  this.toPercent(
                    (temp * 10000) / (height1.offsetHeight * 10000)
                  );
                  var jo = this.toPercent(
                    (temp * 10000) / (height1.offsetHeight * 10000)
                  );
                }

                console.log(jo);
                this.tex = "展开剩余" + jo;
                this.jii = jo;
              }, 0);
            }
          } else {
            this.contentDetail = {};
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    Calculation(type, num1, num2) {
      console.log(type, num1, num2);
      var temp1, temp2, a;
      try {
        // 获取temp1小数点后的长度
        temp1 = num1.toString().split(".")[1].length;
      } catch (e) {
        temp1 = 0;
      }
      try {
        // 获取temp2小数点后的长度
        temp2 = num2.toString().split(".")[1].length;
      } catch (e) {
        temp2 = 0;
      }
      // Math.max(temp1, temp2) 为了获取temp1和temp2两个值中较大的一个
      // Math.pow(a,b) 表示 a 的 b 次方
      a = Math.pow(10, Math.max(temp1, temp2));

      // 计算的方式是先将所有的小数乘为整数，待加减运算执行完之后再除去对应的 a 的值，将其变为小数输出
      // 先判断执行的方式是否是加法，不是的话则执行减法运算
      return type == "add"
        ? (num1 * a + num2 * a) / a
        : (num1 * a - num2 * a) / a;
    },
    toPercent(point) {
      console.log(point, 5);
      // var str=Number(point*100).toFixed(1);
      var str = parseInt(Number(point * 100));
      str += "%";
      return str;
    },
  },
  created() {
    // console.log(this.$store.getters.getId, "89");
    // this.shuangId = this.$store.getters.getId;
    this.shuangId = this.$route.query.id;
    this.getDetailContent();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
<style lang="css" scoped>
