  width: 6.86rem;
  padding: 0.4rem 0.32rem 0.6rem 0.32rem;
  box-sizing: border-box;
  position: relative;
  background-color: #ffffff;
  border-radius: 0.2rem;
  position: relative;
}
.list-one-dec-text {
  width: 6.22rem;
  height: 0.68rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  word-break: break-all;
  font-family: PingFangSC-Medium;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
