  font-family: PingFangSC-Regular;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #3680ff;
  margin-right: 0.1rem;
}
.ab-r {
  width: 0.2rem;
  height: 0.12rem;
}
.list-one-dec1 {
  width: 6.86rem;
  padding: 0.4rem 0.32rem 0.6rem 0.32rem;
  box-sizing: border-box;
  position: relative;
  background-color: #ffffff;
  border-radius: 0.2rem;
  position: relative;
}
.list-one-dec-text1 {
  width: 6.22rem !important;
  text-align: left;
  font-family: PingFangSC-Medium;
  font-size: 0.28rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.44rem;
  letter-spacing: 0rem;
  color: #7a7c8b;
  text-align: justify;
}
