  display: inline-block;
  word-break: break-all;
  white-space: normal;
}
.list-one-dec-text1 >>> span > font {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  word-break: break-all;
  white-space: normal;
}
.list-one-dec-text1 >>> a {
  max-width: 100% !important;
  height: auto;
  /* display: inline-block; */
  word-break: break-all;
  white-space: normal;
}
.list-one-dec-text1 >>> p {
  max-width: 100% !important;
  height: auto;
  margin-left: 0 !important;
  text-indent: 0 !important;
  word-break: break-all;
  white-space: normal;
}
.list-one-dec-text1 >>> table {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  table-layout: fixed;
}
.list-one-dec-text1 >>> table tbody {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
}
.list-one-dec-text1 >>> table tbody tr {
  max-width: 100% !important;
  height: auto;
  display: inline-block;
  overflow-x: scroll;
}
